import * as React from "react"
import { Link } from "gatsby"
import  { StaticImage } from "gatsby-plugin-image"
import JSLogo from "../images/javascript.svg"
import ReactLogo from "../images/react.svg"
import GatsbyLogo from "../images/gatsby.svg"
import NextLogo from "../images/next.svg"

const index = () => {
  return (
    <>
      <div>
        <StaticImage src="../images/index-hero.jpg" alt="hero" quality={90} placeholder="blurred" formats={["AUTO", "WEBP", "AVIF"]}/>
        <div>
          <h1>I'm Abe Hiroki!</h1>
          <h3>JavaScript Developer</h3>
        </div>
      </div>
      <div>
        <div>
          <div>
            <h2>JavaScript Nerd</h2>
            <p>aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa</p>
          </div>
          <StaticImage src="../images/profile.jpg" alt="hero" quality={90} placeholder="blurred" formats={["AUTO", "WEBP", "AVIF"]}/>
        </div>
      </div>
      <div>
        <h2>Skills</h2>
        <div>
          <div><img src={JSLogo} alt="JavaScript"/><span>JavasScript / 10years</span></div>
          <div><img src={ReactLogo} alt="React"/><span>React / 5years</span></div>
          <div><img src={GatsbyLogo} alt="Gatsby"/><span>Gatsby / 3years</span></div>
          <div><img src={NextLogo} alt="Next"/><span>Next.JS / 3years</span></div>
        </div>
        <div>
          <Link to="/contact">Make It Happen!</Link>
        </div>
      </div>
    </>
  )
}

export default index